.help-message {
  padding: 10px;
  font-size: 14px;
}

#tests {
  font-family: sans-serif;
}

#mocha-stats {
  display: none;
}

#mocha .test pre {
  height: 0;
  display: none;
}

#mocha .suite a, #mocha .replay, #mocha .suite h2 {
  pointer-events: none;
}

#mocha h2, p {
  font-size: 14px;
}

#mocha .pending h2 {
  color: #000;
}

#tests {
  width: 900px;
  margin: auto;
}

.uncompleted {
  color: gray;
  pointer-events: none;
}

.completed {
  color: green;
}

.align-right {
  float: right;
  margin-right: 10px;
}

#random-password-list p, .login-container {
  text-align: center;
}

#random-passwords-body {
  width: 80%;
  flex-wrap: wrap;
  justify-content: center;
  margin: 10px auto;
  display: flex;
}

.fake-password, .real-password {
  border: 1px solid #000;
  margin: 0;
  padding: 3px;
}

.test {
  user-select: none;
}

.login-container #name-input, .login-container #password-input {
  margin: 2px;
}

